<template>
  <div class="transaction-detail-table-6">
    <el-table
      :data="data"
      tooltip-effect="dark">
      <el-table-column
        prop="create_time_full"
        :label="$t('assets.transDetail.thead.table6.text1')">
      </el-table-column>
      <el-table-column
        prop="currency_mark"
        :label="$t('assets.transDetail.thead.table6.text2')">
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table6.text3')">
        <template slot-scope="scope">
          <!-- 划转方向 -->
          {{ transfer_type(scope.row.transfer_type) }}
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table6.text4')">
        <template slot-scope="scope">
          <!-- 划转数量 -->
          {{ scope.row.num }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        prop="transfer_id"
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table6.text5')">
      </el-table-column>
      <el-table-column
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table6.text6')">
        <template slot-scope="scope">
          {{ status(scope.row.status) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'transaction-detail-table-6',
    components: {
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      },
      type: {
        type: String
      }
    },
    data() {
      return {
      }
    },
    methods: {
      transfer_type(val) {
        if (val < 0 || val > 1) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        // type值为0,1时，显示“法币到币币”，或“币币到法币”
        const map = [
          this.$t('assets.transDetail.tbody.transTypeFilter.text1'),
          this.$t('assets.transDetail.tbody.transTypeFilter.text2')
        ]
        return map[val]
      },
      status(val) {
        if (val < -2 || val > 8) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        if (this.type === 'ctc') { // -1至2
          const map = [
            this.$t('assets.transDetail.tbody.orderStatusFilter.ctc.text1'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.ctc.text2'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.ctc.text3'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.ctc.text4')
          ]
          return map[val + 1]
        }
        if (this.type === 'otc') { // -2至8，缺少-1因此-1返回异常
          const map = [
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text1'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text2'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text3'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text4'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text5'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text6'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text7'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text8'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text9'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text10'),
            this.$t('assets.transDetail.tbody.orderStatusFilter.otc.text11')
          ]
          return map[val + 2]
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .transaction-detail-table-6{
    
  }
</style>



